@import url('https://fonts.googleapis.com/css2?family=Playwrite+ES+Deco+Guides&display=swap');
.App {
  text-align: center;
}

.App-logo {
  width: 40vmin;
  height: 40vmin;
  pointer-events: none;
  border-radius: 50%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

h2.welcome{
  font-family: "Playwrite ES Deco Guides", serif;
  font-weight: 400;
  font-style: normal;
}
